































.pager {
    text-align: center;
    margin-bottom: 30px;
    font-size: 13px;

    button {
        border: none;
        margin: 0 15px;

        &:hover {
            text-decoration: underline;
        }
    }

    a, span {
        color: #333;
        padding: 3px 10px;
        margin: 0 3px;
    }

    a {
        &:hover, &.active {
            background-color: #eee;
        }
    }
}

