


































































.article-list {
    padding-top: 1px;
    width: 100%;
    min-height: calc(100vh - 60px);

    .item {
        position: relative;
        padding-top: 20px;
        padding-bottom: 30px;

        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 50px;
            right: 50px;
            border-bottom: 1px dashed #ccc;
        }

        &:nth-last-of-type(2) {
            &:after {
                display: none;
            }
        }

        .summery {
            text-align: center;
            margin-bottom: 20px;
            color: #999;
        }

        .read-article {
            display: block;
            width: 100px;
            text-align: center;
            padding: 3px;
            margin: 0 auto;
            border: 2px solid #555;
            color: #555;
            font-size: 13px;
            transition: all .5s;

            &:hover {
                border-color: #000;
                background-color: #000;
                color: #fff;
            }
        }
    }

    .top {
        margin-bottom: 30px;
        line-height: 30px;
    }
}
